.footer {
  background-color: #798F86;
  color: #3B3838;
  padding: 2rem 0 0; /* Reduced from 60px */
  width: 100%;
  font-size: 0.9rem; /* Slightly smaller text */
}

.footer-container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  gap: 1rem; /* Better spacing control */
}

.footer-section, .footer-contact {
  flex: 1;
  min-width: 150px; /* Prevent squeezing */
  margin-bottom: 1.5rem;
}

.footer-section h4, .footer-contact h4 {
  margin-bottom: 0.75rem; 
  font-size: 1rem;
  font-weight: 600;
}
.footer-contact p {
  margin-bottom: 8px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section li {
  margin-bottom: 0.5rem; /* Tighter line spacing */
}

.footer-section a {
  color: #3B3838;
  text-decoration: none;
  transition: color 0.2s;
}

.footer-section a:hover {
  color: #A6645D;
}

.contact-info p {
  margin: 0.25rem 0; /* Reduced paragraph spacing */
  line-height: 1.4;
}

.footer-bottom {
  text-align: center;
  padding: 1rem 0;
  border-top: 1px solid rgba(59, 56, 56, 0.2);
  margin-top: 1rem;
  font-size: 0.8rem;
}